@import url('https://fonts.googleapis.com/css2?family=Abhaya+Libre&family=Cormorant+Garamond:wght@400;500;600&family=Courgette&family=Lexend+Exa&family=Tangerine:wght@400;700&display=swap');

* {
  margin: 0 auto;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: black;
  --color-primary: #6d6d6d;
  --color-secondary: #995b3c;
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;

  --baseline: 12px;
}

html {
  overflow: hidden;
  margin: 0;
  padding: 0;
}

/* add background-image for web */
/* import font */
body {
  margin: 0 auto;
  display: flex;
  height: -webkit-fill-available;
  height: -moz-available;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Lexend Exa', 'courgette', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.7;
  overflow: hidden;
}

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

/* section {
  margin-top: 8rem;
} */

section>h2,
section>h5 {
  text-align: center;
  color: var(--color-light);
}

div>h2 {
  color: var(--color-primary);
  font-weight: 300;
  font-size: 1.1rem;
}

form>p {
  color: var(--color-primary);
  font-size: 0.8rem;
  margin-bottom: 0.8rem;
}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white)
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
}

img {
  width: 100%;
  object-fit: cover;
}

.text-malay {
  color: var(--color-primary);
  font-size: 0.75rem;
}

.text-english {
  color: var(--color-secondary);
  font-size: 0.65rem;
}

